/* my account new updation */
:root {
  --primary: #0077FF;
  --dark: #000;
}

body {
  height: 100vh;
  display: grid;
  font-family: Roboto;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
}

* {
  box-sizing: border-box;
}

.inp {
  position: relative;
  margin: auto;
  width: 100%;
  max-width: 280px;
  border-radius: 3px;
  overflow: hidden;
}

.inp .label {
  position: absolute;
  top: 20px;
  left: 12px;
  font-size: 16px;
  color: rgba(var(--dark), 0.5);
  font-weight: 500;
  transform-origin: 0 0;
  transform: translate3d(0, 0, 0);
  transition: all 0.2s ease;
  pointer-events: none;
}

.inp .focus-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(var(--dark), 0.05);
  z-index: -1;
  transform: scaleX(0);
  transform-origin: left;
}

.inp input {
  /* -webkit-appearance: none; */
  appearance: none;
  width: 100%;
  border: 0.1;
  font-family: inherit;
  padding: 16px 12px 0 12px;
  height: 50px;
  font-size: 16px; 
   font-weight: 400;
   background: rgba(var(--dark), 0.02); 
  box-shadow: inset 0 -1px 0 rgba(var(--dark), 0.3);
  color: var(--dark);
  transition: all 0.15s ease; 
}

.inp input:hover {
  background: rgba(var(--dark), 0.04);
  box-shadow: inset 0 -1px 0 rgba(var(--dark), 0.5);
}

.inp input:not(:placeholder-shown) + .label {
  color: rgba(var(--dark), 0.5);
  transform: translate3d(0, -12px, 0) scale(0.75);
}

.inp input:focus {
  background: rgba(var(--dark), 0.05);
  outline: none;
  box-shadow: inset 0 -2px 0 var(--primary);
}

.inp input:focus + .label {
  color: var(--primary);
  transform: translate3d(0, -12px, 0) scale(0.75);
}

.inp input:focus + .label + .focus-bg {
  transform: scaleX(1);
  transition: all 0.1s ease;
}
/*  my account update password pop up*/
.password-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
