  margin-top: 10px;
  /* border: 1px solid gray; */
  border-radius: 3px;
  margin-bottom: 5px;
}

.password-popup form button {
  margin-top: 10px;
  /* width: 200px; */
}
.password-popup form .popup-button {
  margin-top: 10px;
  width: 150px;
  padding: 12px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
