  background-color: #007bff;
  cursor: pointer;
  transition: background-color 0.3s;
}

.password-popup form .popup-button + .popup-button {
  margin-left: 10px; /* Add a gap of 10px between the buttons */
}

.password-popup form .cancel-button {
  background-color: #dc3545; /* Change the background color of the cancel button */
}

.password-popup form .cancel-button:hover {
  background-color: #c82333; /* Change the hover background color of the cancel button */
}


