/*checklist animation on home page */
.table-hover-animation {
  border-collapse: separate;
  border-spacing: 0;
}
.table-hover-animation tbody tr {
  transition: background-color 0.3s ease;
  cursor: pointer;
}
.table-hover-animation tbody tr:hover {
  background-color:#F3EAF8;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}
/* CSS for the checkbox animation */
.table-hover-animation input[type="checkbox"] {
  transition: transform 0.5s ease;
}
.table-hover-animation input[type="checkbox"]:hover {
  transform: scale(1.3);
}
.table-hover-animation input[type="checkbox"]:checked {
  transform: scale(1);
}
