/* admin home page */
.admin-dashboard {
  text-align: center;
  padding: 30px;
}
.dashboard-title {
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 40px;
  color: #333;
}
.counting-container {
  display: flex;
  justify-content: center;
}
.counting-item {
  margin: 0 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.counting-number {
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 48px;
  font-weight: bold;
  color: #fff;
  background-color: #333;
  border-radius: 50%;
  animation: counting-animation 1.5s ease-in-out;
}
.counting-label {
  margin-top: 10px;
  color: #777;
}
@keyframes counting-animation {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

